

ul.checklist {
  list-style: none;
  margin-left: 0.2rem;
}
ul.checklist li:before {
  content: '';
  display: inline-block;
  height: 0.95rem;
  width: 0.95rem;
  background-image: url('../../../img/util/tick.svg');
  background-size: contain;
  background-repeat: no-repeat;
  padding-left: 1.2em;
  position: relative;
  top: 2px;
}

